.map-photo {
  display: block;
  min-width: 320px;
  height: 350px;
  background-image: url("./img.index.map.jpg") no-repeat;

  @media (min-width: @tablet-width) {

    min-width: 768px;
    height: 350px;
  }
}

.map iframe {
  border: none;
  display: block;
  width: 100%;
  height: 350px;

  @media (min-width: @tablet-width) {
    min-width: 768px;
    height: 350px;
  }
}
